/* Reset and Body styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, sans-serif;
  line-height: 1.6;
  background:linear-gradient(blue orange);
  color: #333;
}

/* Navbar styles */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 20px;
  background-color: #4CAF50;
  color: white;
}

.nav-links a {
  margin-left: 15px;
  color: white;
  font-weight: bold;
}

.navbar h1 {
  font-size: 24px;
}

/* Footer styles */
footer {
  text-align: center;
  padding: 20px;
  background-color: #333;
  color: white;
}

.social-icons {
  margin-top: 10px;
}

/* Homepage styles */
.homepage {
  padding: 20px;
  text-align: center;
  background:linear-gradient(blue orange)
}

.homepage img {
  max-width: 100%;
  border-radius: 10px;
}

/* Categories and Menu Item styles */
.categories {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  padding: 20px;
  justify-content: center;
}

.category-card {
  width: 200px;
  border: 1px solid #ddd;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
  background-color: white;
}

.category-card img {
  width: 100%;
  height: 150px;
  object-fit: cover;
}

.category-card h3 {
  padding: 10px;
  color: #333;
}

.category-card p {
  margin: 10px;
  font-size: 0.9em;
}

.category-card button {
  background-color: #4CAF50;
  color: white;
  border: none;
  padding: 10px;
  width: 100%;
  cursor: pointer;
}

.category-card button:hover {
  background-color: #45a049;
}

/* Cart styles */
.cart-items {
  list-style-type: none;
  padding: 20px;
  max-width: 600px;
  margin: auto;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.cart-items li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  border-bottom: 1px solid #ddd;
}

.cart-items button {
  background-color: #FF6F61;
  color: white;
  border: none;
  cursor: pointer;
  padding: 5px 10px;
}

.cart-items button:hover {
  background-color: #ff4c39;
}

.cart-total {
  font-size: 1.2em;
  font-weight: bold;
  padding: 15px;
  text-align: right;
}

.checkout-button {
  display: block;
  margin: 20px auto;
  padding: 10px 20px;
  background-color: #4CAF50;
  color: white;
  border: none;
  cursor: pointer;
  font-size: 1.1em;
  text-align: center;
}

.checkout-button:hover {
  background-color: #45a049;
}
