/* src/styles.css */
.contact {
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 8px;
  margin: 20px 0;
}

h2 {
  color: #333;
  font-size: 2em;
  margin-bottom: 20px;
}

form {
  display: flex;
  flex-direction: column;
}

label {
  margin-bottom: 15px;
  font-size: 1.1em;
}

input, textarea {
  padding: 10px;
  margin-top: 5px;
  border-radius: 4px;
  border: 1px solid #ccc;
  font-size: 1em;
}

input[type="text"], input[type="email"] {
  width: 100%;
}

textarea {
  width: 100%;
  height: 150px;
  resize: vertical;
}

button {
  padding: 10px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 4px;
  font-size: 1.2em;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #45a049;
}

.success-message {
  padding: 20px;
  background-color: #e0ffe0;
  border: 1px solid #4CAF50;
  border-radius: 5px;
  color: #4CAF50;
  font-size: 1.2em;
}